.App {
  font-family: sans-serif;
  text-align: center;
  background-color: rgb(226, 174, 174);
  margin-bottom: auto;
}

h1 {
  font-size: 1.8rem;
  font-weight: bold;
  color: #651983;
}

input {
  margin-top: 20px;
  width: 80%;
  padding: 0.8rem;
  margin-bottom: 2rem;
}

.meaning {
  font-size: 1.8rem;
  font-weight: bold;
}

.animals {
  margin-top: 20px;
}

button {
  padding: 0.5rem 0.8rem;
  background-color: #651983;
  color: rgb(226, 174, 174);
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: bold;
  margin: 0.3rem;
  border: 2px solid #07aecc;
  border-radius: 5px;
  transition: all 0.2s ease;
  text-transform: uppercase;
  margin-bottom: 1.5rem;
}

button:hover {
  transform: translateY(5px);
}

button:active {
  transform: scale(1.1);
}

button:focus {
  outline: none;
}
